import React from 'react';

const TransactionCard = ({ transaction }) => {
  console.log('transaction :', transaction);
  return transaction.map(({ id, type, amount, currency }) => (
    <tr key={id}>
      <td>{type}</td>
      <td>{amount}</td>
      <td>{currency}</td>
    </tr>
  ));
};
export default TransactionCard;
