import React, { Fragment } from 'react';
import css from './Friends.module.css';
import Card from '../friends/Card';

const Friends = ({ friends }) => {
  return (
    <ul className="friend-list">
      {friends.map(e => (
        <Card key={e.id} friends={e} />
      ))}
    </ul>
  );
};

export default Friends;
